import {
	Grid,
	Table,
	TableBody,
	TableContainer,
	Alert,
	useTheme,
	Button,
	Stack,
	Divider,
	CardContent,
	CardActions,
} from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { useQuery, gql } from '@apollo/client';
import { useEffect, useState } from 'react';
import AnimateButton from '../../../ui-component/extended/AnimateButton';

import SubCard from '../../../ui-component/cards/SubCard';
import { gridSpacing } from '../../../store/constant';
import { TProperty } from '../../../types';
import { getProperty } from '../../../graphql/queries';
import Loader from '../../../ui-component/Loader';
import TextRow from '../components/TextRow';
import UploadFile, { TFile } from '../components/UploadFile';

type Props = {
	id: string;
};

const PropertyEdit = ({ id }: Props) => {
	const { loading, error, data } = useQuery(gql(getProperty), {
		variables: { PropertyId: id },
		errorPolicy: 'all',
	});
	const property = _get(data, 'getProperty', {});
	const propertyThumbnail = _get(property, 'Thumbnail', null);
	const [images, setImages] = useState<TFile[]>([]);
	const theme = useTheme();

	useEffect(() => {
		if (!propertyThumbnail) return;

		const { presignedUrl = '', name = '' } = propertyThumbnail;
		const thumbnail = Object.assign(new File([''], name), {
			preview: presignedUrl,
		});
		setImages([thumbnail]);
	}, [propertyThumbnail]);

	if (loading) return <Loader />;

	if (error && _isEmpty(property)) {
		return (
			<Alert severity='error' sx={{ color: theme.palette.error.main }}>
				Property not found
			</Alert>
		);
	}

	const onUploadFile = (acceptedFiles: File[]) => {
		setImages(
			acceptedFiles.map(file =>
				Object.assign(file, {
					preview: URL.createObjectURL(file),
				})
			)
		);
	};

	useEffect(
		() => () => {
			// Make sure to revoke the data uris to avoid memory leaks
			images.forEach(file => file.preview && URL.revokeObjectURL(file.preview));
		},
		[images]
	);

	return (
		<>
			<CardContent>
				<Grid container spacing={gridSpacing}>
					<Grid item lg={6} xs={12}>
						<SubCard title='Property Information'>
							<TableContainer>
								<Table
									sx={{
										'& td': {
											borderBottom: 'none',
										},
									}}
									size='small'
								>
									<TableBody>
										<TextRow<TProperty>
											data={property}
											source='PropertyNo'
											label='Property No'
										/>
										<TextRow<TProperty>
											data={property}
											source='PropertyData.StreetAddress'
											label='Street Address'
										/>
										<TextRow<TProperty>
											data={property}
											source='PropertyData.County'
											label='County'
										/>
										<TextRow<TProperty>
											data={property}
											source='PropertyData.State'
											label='State'
										/>
										<TextRow<TProperty>
											data={property}
											source='PropertyData.Zip'
											label='Zip'
										/>
										<TextRow<TProperty>
											data={property}
											source='PropertyData.City'
											label='City'
										/>
									</TableBody>
								</Table>
							</TableContainer>
						</SubCard>
					</Grid>
					<Grid item lg={6} xs={12}>
						<SubCard title='Property Photo'>
							<UploadFile files={images} onDrop={onUploadFile} accept='.pdf' />
						</SubCard>
					</Grid>
				</Grid>
			</CardContent>
			<Divider />
			<CardActions>
				<Grid item xs={12}>
					<Stack direction='row' justifyContent='flex-end'>
						<AnimateButton>
							<Button color='primary' variant='contained' type='submit'>
								Update
							</Button>
						</AnimateButton>
					</Stack>
				</Grid>
			</CardActions>
		</>
	);
};

export default PropertyEdit;
